import { ProcessedWorkLog } from '../../../types';
import { TimeUtils } from '../../../lib/timeUtils';

interface Env {
    DB: any; // D1Database might not be in scope, use any to suppress
}

export const onRequestPost = async (context: any) => {
    try {
        const { request, env } = context;
        const logs = await request.json() as ProcessedWorkLog[];

        if (!logs || !Array.isArray(logs) || logs.length === 0) {
            return new Response(JSON.stringify({ success: false, message: 'No logs provided' }), { status: 400 });
        }

        // 1. Extract Unique Users
        const uniqueUsers = new Map<string, { name: string; dept?: string; title?: string }>();
        logs.forEach(log => {
            // Use Name as key for simplicity in this context, or userId if it looks like a real ID
            // The excel parser generates "user_1", "user_2". These are NOT stable IDs.
            // We MUST rely on Name.
            if (log.userName) {
                uniqueUsers.set(log.userName, {
                    name: log.userName,
                    dept: log.department,
                    title: log.userTitle
                });
            }
        });

        const staffIdMap = new Map<string, string>(); // Name -> UUID

        // 2. Upsert Staff into project_staff and get IDs
        for (const [name, info] of uniqueUsers.entries()) {
            // Check if exists
            const existing = await env.DB.prepare("SELECT id FROM project_staff WHERE name = ?")
                .bind(name)
                .first();

            let staffId = existing?.id;

            if (!staffId) {
                staffId = crypto.randomUUID();
                // Insert
                await env.DB.prepare(`
                INSERT INTO project_staff(id, company_id, name, target_persona, risk_level)
VALUES(?, ?, ?, ?, ?)
            `).bind(
                    staffId,
                    'comp_eluon', // Default Company
                    name,
                    'Regular Employee', // Marker
                    'low'
                ).run();
            }

            staffIdMap.set(name, staffId);
        }

        // 3. Prepare Batch Insert for Logs
        const statements = [];

        // First, determine date range to clean up existing logs for these users
        // Actually, safer to delete day-by-day or just delete all for these staff in the min-max range.
        // Let's just INSERT OR REPLACE or Delete-Then-Insert.
        // To avoid complexity, let's delete logs for these users on these dates.

        // Group by staff to optimize deletes? No, simple delete per log is inefficient.
        // Let's iterate logs.

        for (const log of logs) {
            const staffId = staffIdMap.get(log.userName);
            if (!staffId) continue;

            // Date format correction: Excel might give "YYYY-MM-DD" or others.
            // ProcessedWorkLog usually has `date` field? 
            // Wait, ProcessedWorkLog has `date` (string).

            // Convert Minutes to HH:mm for storage
            const startTimeStr = TimeUtils.minutesToColonFormat(log.startTime || 0);
            const endTimeStr = TimeUtils.minutesToColonFormat(log.endTime || 0);

            // Delete existing for this user+date
            statements.push(
                env.DB.prepare("DELETE FROM project_staff_work_logs WHERE staff_id = ? AND work_date = ?")
                    .bind(staffId, log.date)
            );

            // Insert new
            statements.push(
                env.DB.prepare(`
                INSERT INTO project_staff_work_logs(
    id, staff_id, work_date, start_time, end_time, status
) VALUES(?, ?, ?, ?, ?, ?)
            `).bind(
                    crypto.randomUUID(),
                    staffId,
                    log.date,
                    startTimeStr,
                    endTimeStr,
                    log.logStatus || 'NORMAL'
                )
            );
        }

        // Execute Batch
        // D1 batch limit is usually 128 queries. We might need to chunk.
        const BATCH_SIZE = 50;
        for (let i = 0; i < statements.length; i += BATCH_SIZE) {
            const batch = statements.slice(i, i + BATCH_SIZE);
            await env.DB.batch(batch);
        }

        return new Response(JSON.stringify({
            success: true,
            message: `Successfully saved ${logs.length} logs for ${uniqueUsers.size} staff members.`
        }), { status: 200 });

    } catch (err: any) {
        console.error('Save logs error:', err);
        return new Response(JSON.stringify({ success: false, message: err.message || String(err) }), { status: 500 });
    }
};

